/* Layout & Landing Page */
.header-color {
  background: #00B4DB;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #0083B0, #00B4DB);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #0083B0, #00B4DB); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid {
  background: #00B4DB;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #0083B0, #00B4DB);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #0083B0, #00B4DB); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

}

.contact-view-button {
  width: 100%;
  text-align: center;
  color: white;
  background: #27221F;
}

.contact-view-button h1 {
  margin: 0;
}

.image-me {
  height: 250px;
  margin-top: 5em;
  width: 250px;
  border-radius: 100px;
}

.banner-text {
  background-color: #000000;
  opacity: 0.65;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  color: #ffffff;
  font-size: 66px;
  font-weight: bold;
}

.about-me {
  color: #b8d8f0;
  font-size: 66px;
  font-weight: 500;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: #ffffff;
  font-size: 20px;
  padding: 1em;
}
.social-links {
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: auto
}

.social-links i {
  color: #ffffff;
  font-size: 5em;
  padding: 8px
}


.layout {
  background: rgb(224, 227, 224);
}
/* ------- END -------- */

/* --------Contact Page ---------- */
.contact-body {
  margin: auto;
  height: 100%;
  width:100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 85%;
  height: 75%;
  background: white;
  color: black;
  border: 2px solid #0083B0;
  border-radius: 10px;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 66px;
  padding-right: 5rem;
}
/* ------ END ------- */

/* ------ Resume --------- */
.resume-right-col {
  background: #27221F;
  color: white;
  padding: 2em;
}
/* ------ END ------- */

.cards {
  display: flex;
  flex-wrap: wrap;
}

.card {
  margin: 10px;
}

.mdl-card__title-text {
  background: rgba(0,0,0,.5);
  border-radius: 5px;
}

@media (max-width: 839px) and (min-width: 480px) {
  .banner {
    margin-left: 18em;
  }
  .about-me {
    margin-left: 9pc
  }
}